<template>
  <div class="inputBox">
    <input type="text" v-model="newTodoItem" placeholder="할 일을 입력하세요" />
    <button type="button" class="addBtn" @click="addTodo">추가</button>
    <ErrorModal v-if="showModal" @close="showModal = false">
      <template v-slot:header>주의하세요</template>
      <template v-slot:footer>
        <p>할 일을 반드시 입력하셔야 합니다.</p>
        <button class="closeBtn" @click="showModal = false">닫기</button>
      </template>
    </ErrorModal>
  </div>
</template>

<script>
import ErrorModal from "./ErrorModal.vue";

export default {
  data() {
    return {
      newTodoItem: "",
      showModal: false,
    };
  },
  methods: {
    addTodo() {
      if (this.newTodoItem.trim() !== "") {
        let value = this.newTodoItem.trim();
        this.$emit("addTodo", value); // app.vue로 전달(addTodo이벤트, 전송데이터)
        this.clearInput();
      } else {
        this.showModal = true;
      }
    },
    clearInput() {
      this.newTodoItem = "";
    },
  },
  components: {
    ErrorModal,
  },
};
</script>

