<template>
  <section>
    <transition-group name="list" tag="ul">
      <li v-for="(todoItem, index) in propsData" :key="todoItem">
        {{ todoItem }}
        <button
          @click="removeTodo(todoItem, index)"
          class="removeBtn"
          type="button"
        >
          -
        </button>
        <!-- removeTodo event -->
      </li>
    </transition-group>
  </section>
</template>

<script>
export default {
  name: "TodoList",
  props: ["propsData"],
  methods: {
    removeTodo(todoItem, index) {
      this.$emit("removeTodo", todoItem, index);
    },
  },
};
</script>
<style>
ul {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: left;
}
li {
  min-height: 50px;
  height: 50px;
  line-height: 50px;
  margin: 0.6rem 0;
  padding: 0 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.removeBtn {
  margin-left: auto;
  color: #d44;
}

.list-enter-active,
.list-leave-active {
  transition: all 0.6s;
}

.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateY(20px);
}
</style>