<template>
  <div class="clearContainer">
    <button type="button" class="clearAll" @click="clearTodo">전체 삭제</button>
  </div>
</template>
<script>
export default {
  name: "TodoFooter",
  methods:{
    clearTodo(){
        this.$emit('removeAll');
    }
  }
};
</script>
<style>
    .clearContainer{
        height:50px;
        padding: 40px 0;

    }
    .clearAll{
        width: 200px;
        height: 50px;
        line-height: 50px;
        background: #039;
        border:0 none;
        border-radius: 5px;
        color: #fff;
    }
</style>